








import Vue from "vue";
import marked from "marked";
export default Vue.extend({
  props: {
    id: String,
    company: String,
    role: String,
    description: String,
    start: String,
    end: String,
  },
  mounted() {
    if (!("rawHighlight" in window)) {
      const store = this.$store;
      //Messy hack to inject $store into dynamically generated links from marked
      (window as any).rawHighlight = function (ev: MouseEvent) {
        console.log("called");
        const href = (ev.target as HTMLAnchorElement).href;
        console.log(href);
        store.dispatch("highlight", href.slice(href.lastIndexOf("#") + 1));
      };
    }
  },
  computed: {
    timeRange(): string {
      return `${this.toMonth(
        +this.start.substring(0, 2)
      )} ${this.start.substring(3)} to ${
        this.end == null
          ? "Present"
          : `${this.toMonth(+this.end.substring(0, 2))} ${this.end.substring(
              3
            )}`
      }`;
    },
    markedDesc(): string {
      return marked(this.description, { gfm: true, breaks: true }).replaceAll(
        '<a href="#',
        `<a onclick='window.rawHighlight(event)' href="#`
      );
    },
    highlighted(): boolean {
      return this.$store.state.highlighted === this.id;
    },
  },
  methods: {
    toMonth(monthNumber: number) {
      return new Date(2020, monthNumber - 1, 1).toLocaleString("default", {
        month: "long",
      });
    },
  },
});
